'use client';

import ReactApexChart from "react-apexcharts";

import  Select  from 'react-select';

import { useState, useRef, useEffect } from "react";

import { 
    resetStatus,
    getDashboardData,
    selectStatus,
    selectData,
  } from '@/lib/features/document_ocr/dashboard.slice'
  
import { useAppDispatch, useAppSelector } from "@/lib/hooks/hooks";

export function ColumnsChart() {

    const dispatch = useAppDispatch();

    const status = useAppSelector(selectStatus);
    const data = useAppSelector(selectData);

    const initialized = useRef(false);

    const optionsCurrency = [
      { value: 'jpy', label: '¥ JPY' },
      { value: 'usd', label: '$ USD' },
      { value: 'euro', label: '€ Euro' }
    ]

    const chartConfig = {
          
        series: [{
          name: 'Expenses',
          data: data.values,
        }],
        options: {
          chart: {
            height: 350,
            type: 'bar',
          },
          plotOptions: {
            bar: {
              borderRadius: 0,
              // distributed: true,
              dataLabels: {
                position: 'top', // top, center, bottom
              },
            }
          },
          colors: ['#616C38'],
          dataLabels: {
            enabled: true,
            formatter: function (val : any) {
              return currency.label.split(' ')[0]  + ' ' + val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            },
            offsetY: -30,
            style: {
              fontSize: '17px',
              colors: ["#304758"]
            }
          },
          
          xaxis: {
            categories: data.labels,
            position: 'bottom',
            axisBorder: {
              show: false
            },
            axisTicks: {
              show: false
            },
            crosshairs: {
              fill: {
                type: 'gradient',
                gradient: {
                  colorFrom: '#D8E3F0',
                  colorTo: '#BED1E6',
                  stops: [0, 100],
                  opacityFrom: 0.4,
                  opacityTo: 0.5,
                }
              }
            },
            tooltip: {
              enabled: true,
            }
          },
          yaxis: {
            labels: {
              show: true,
              formatter: function (val: any) {
                return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
              }
            }
          
          },
          title: {
            text: 'Total Expenses by Category',
            align: 'center',
            style: {
              color: '#444'
            }
          }
        },
    };

    const [chart, setChart] = useState(chartConfig);
    const [currency, setCurrency] = useState(optionsCurrency[0]);

    useEffect(() => {
    
        if (!initialized.current) {
            initialized.current = true;
        }
        console.log("Chart Values >> ", chartConfig.series[0].data);

        setChart(chartConfig);

      }, [data]);

    useEffect(() => {
  
        // setChart(chartConfig);

      }, [currency]);

    const setNewCurrency = (data: any) => {
      console.log("SELECT Currency >> ", data.label.split(' ')[0]);
      setCurrency(data);
      dispatch(getDashboardData({currency: data.value}));
    }

    return(
        <div>
            <div>
                <div className="flex justify-end mb-2">
                    <div className="w-1/4 flex flex-row justify-end items-center">
                      <label className="basis-1/5">Currency :</label>
                      <Select name="type" className="w-30 basic-single" classNamePrefix="select" options={optionsCurrency} defaultValue={optionsCurrency[0]} isClearable={false} onChange={setNewCurrency} />
                    </div>
                </div>
                <ReactApexChart options={chart.options} series={chart.series} type="bar" height={350} />
            </div>
        </div>
    );
}